export let canvas
export let renderer
export let scene
export let camera
export const camDefaultPos=new THREE.Vector3(250,200,250)
export let controls
export const section3dObjs={}
export const section3dObjects={}
export let cameraPos

export let yPos
export let yLen
export let zuLen
export let vFromY
export let eaLen
export let yzInitAngle

export let wristAffector3dObject
export let wristAffectorControls

export let endAffectorOffset=new THREE.Vector3(12.5,0,0)
export let endAffector3dObject
export let endAffectorStart3dObject
export let endAffectorControls
export const vw=new THREE.Vector3(5.423,0,0)
export let endAffectorActual3dObject

export const loadingManager=new THREE.LoadingManager()
export const exrLoader=new EXRLoader(loadingManager)
export const textureLoader=new THREE.TextureLoader(loadingManager)

export const yellowMatcap=exrLoader.load(require('../../assets/imgs/skintone-yellow.exr'))
// export const grayMatcap=exrLoader.load(require('../../assets/imgs/skintone-gray.exr'))
// export const grayMatcap=textureLoader.load(require('../../assets/imgs/6f106fee8cd42335efe16cf4f7250ecf7c284a83.png'))

export const grayMatcap=textureLoader.load(require('../../assets/imgs/d2drhlh-0c1dc8e8-310b-4f13-b1ed-596c78153401-gray.jpg'))

export const yellowMaterial=new THREE.MeshMatcapMaterial({color: new THREE.Color('hsl(50,100%,25%)'),matcap: yellowMatcap})
export const silverMaterial=new THREE.MeshMatcapMaterial({color: new THREE.Color('hsl(0,0%,25%)'),matcap: grayMatcap})
export const blackMaterial=new THREE.MeshMatcapMaterial({color: new THREE.Color('hsl(0,0%,2%)'),matcap: grayMatcap})

// export const yellowMaterial=new THREE.MeshMatcapMaterial({color: new THREE.Color('hsl(0,0%,100%)'),matcap: grayMatcap})
