import React,{useContext,Fragment,useState} from 'react'
import axios from 'axios'
import {Scrollbar} from 'react-scrollbars-custom'

import './RightSidebar.scss'
import {MainContext,homePose,targetPose,realPose} from '../../contexts/MainContext'
import Sixi2,{Sixi2Speeds} from '../../model/Sixi2'
import Accordion from '../../utilities/Accordion/Accordion'

// TODO: End Affector Status
// TODO: Speed & accl
// TODO: Disable sidebar if historySeekPos!==poseHistory

let state,dispatch
export default function RightSidebar(){
	({state,dispatch}=useContext(MainContext))
	return (
		<Scrollbar id="RightSidebar" disableTracksWidthCompensation>
			<div className="row f-vert f-nowrap" style={{height: '100vh'}}>
				{/*<div className="b-600 p-12">Sim Robot Controls</div>*/}
				<div className="accordion-container">
					<Accordion head={'Arm Angles'}>
						{['x','y','z'].map(i=><SectionInput {...{i}} key={i}/>)}
					</Accordion>
				</div>
				<div className="accordion-container">
					<Accordion head={'Wrist Angles'}>
						{['u','v','w'].map(i=><SectionInput {...{i}} key={i}/>)}
					</Accordion>
				</div>
				<div className="accordion-container">
					<Accordion head={'Hand State'}>
						{['t'].map(i=><SectionInput {...{i}} key={i}/>)}
					</Accordion>
				</div>
				<div className="col-extrude row spc-12">
					<div className="button-icon col-12" onClick={()=>dispatch(['SET_SIM_ANGLES',JSON.parse(homePose)])}>
						<div className="icon icon-home text-muted"/>
						<div className="col">Load Home Pose</div>
					</div>
					<div className="button-icon col-12" onClick={()=>dispatch(['SET_SIM_ANGLES',realPose])}>
						<div className="icon icon-joystick text-muted"/>
						<div className="col">Load Real World Pose</div>
					</div>
					<div className="button-icon col-12" onClick={()=>dispatch(['SET_SIM_ANGLES',targetPose])}>
						<div className="icon icon-node_link_direction text-muted"/>
						<div className="col">Load Target Pose</div>
					</div>
					<a
						className="button-icon col-6"
						href={`data:text/json;charset=utf-8,${encodeURIComponent(JSON.stringify(state.simPose))}`}
						download="pose.json">
						<div className="icon icon-arrow_big_down text-muted"/>
						<div className="col">Save</div>
					</a>
					<div className="button-icon col-6">
						<div className="icon icon-arrow_big_up text-muted"/>
						<div className="col">
							<label htmlFor="pose-load">Load</label>
							<input
								type="file"
								id="pose-load"
								accept="application/json"
								hidden
								onChange={onUpload}/>
						</div>
					</div>
				</div>
				<div className="accordion-container">
					<Accordion head={'Speed'}>
						<SpeedControls/>
					</Accordion>
				</div>
				<div className="f-grow-1"></div>
				{/* TODO: Halt*/}
				<div className="col-extrude row spc-12 f-vert">
					{/*<div className="col-extrude row spc-12 f-center">*/}
						{/*<input type="checkbox" checked={state.isPolling} onChange={({target})=>dispatch(['SET_STATE',{isPolling: target.checked}])}/>*/}
						{/*<div>Show live position</div>*/}
						{/*<div className="h7 ml-auto mt-12">{state.isPolling?(state.online?'Online':'Offline'):undefined}</div>
					</div>
					<div className="button outline row f-center" onClick={()=>axios.put(`https://${state.IP}:7878/stop`)}>
						<div className="icon-stop super-center" style={{fontSize: '11px'}}/>
						<div className="col text-center">Emergency Stop</div>
					</div>
					<div className="button outline row f-center" onClick={()=>dispatch(['GO'])}>
						<div className="icon-play super-center"/>
						<div className="col text-center">Go</div>
					</div>*/}
					{/*
						state.showSequenceControl?
							<div className="button outline row f-center">
								<div className="fa fa-forward super-center"/>
								<div className="col text-center">Play Sequence</div>
							</div>:undefined
					*/}
					{state.isPolling?<div className={'status h7 ml-auto mb-0 mr-12 '+(state.online?'online':'offline')}/>:undefined}
					<div className="controls">
						{/*<div className="h7">{state.isPolling?(state.online?'Online':'Offline'):undefined}</div>*/}
						<div style={{height: '23px'}}/>
						<div className="go" onClick={()=>dispatch(['GO'])}>Go</div>
						<div className="button-icon" onClick={()=>axios.put(`https://${state.IP}:7878/stop`)}>
							<div className="icon icon-stop" style={{fontSize: '11px'}}/>
							<div className="f-grow-1">Stop</div>
						</div>
					</div>
					{state.showSequenceControl?
						<div className="button-icon" onClick={()=>axios.put(`https://${state.IP}:7878/batch`,state.sequencePoses.map(v=>JSON.parse(v)))}>
							<div className="fa fa-forward text-muted" style={{transform: 'scaleY(.75)'}}/>
							<div className="col">Play Sequence</div>
						</div>:undefined}
				</div>
			</div>
		</Scrollbar>
	)

	function onUpload({target}){
		const reader=new FileReader()
		reader.onload=({target})=>dispatch(['SET_SIM_ANGLES',JSON.parse(target.result)])
    reader.readAsText(target.files[0])
	}
}

function SectionInput({i}){
	const section=Sixi2[i]
	const [resetActive,setResetActive]=useState(false)
	return (
		<div className="p-8 pt-2">
			<div className="row mt-0">
				<label className="uppercase">{i}</label>
				<div className="col" style={{height: '30px'}}>
					<input
						type="range"
						min={section.angles.min}
						max={section.angles.max}
						value={+state.simPose[i].toFixed(1)}
						{...{onChange}}
						step=".1"/>
				</div>
			</div>
			<div className="trim sliding-row">
				<div className={'row f-nowrap mt-0'+(resetActive?' active':'')}>
					<div className="col-6 row mr-8">
						<div className="button outline fa fa-repeat p-0 text-muted" title="Reset" onClick={()=>setResetActive(true)}/>
						<div className="pl-10 col row f-justify-space-between">
							<div className="col text-muted h7">{+section.angles.min.toFixed(1)}</div>
							{/*<div className="col text-center b-500">{state.simPose[i]}</div>*/}
							{/*<input className="text-center py-2" type="number" pattern="-?[0-9]+(\.[0-9]{0,3})?" value={state.simPose[i]} {...{onChange}}/>*/}
							<div className="button-icon f-self-center" style={{height: '20px'}}>
								<div className="icon h7 text-muted" style={{fontFamily: 'inherit'}}>{+section.angles.home.toFixed(1)}</div>
								<div className="p-0">
									<input className="text-center py-2" type="number" value={+state.simPose[i].toFixed(1)} {...{onChange}}/>
								</div>
							</div>
							<div className="col text-muted h7 text-right">{+section.angles.max.toFixed(1)}</div>
						</div>
					</div>
					<div className="col-6 row mr-8 f-center">
						<div className="button outline icon-cross_mark" onClick={()=>setResetActive(false)}/>
						<div className="mr-auto ml-8">Reset to:</div>
						<button title="Home angle" className="outline ml-8 h8 icon-home" style={{height: '22px'}} onClick={()=>onClick(JSON.parse(homePose)[i])} tabIndex="-1"/>
						<button title="Actual angle" className="outline ml-8 h8 icon-joystick" style={{height: '22px'}} onClick={()=>onClick(realPose[i])} tabIndex="-1"/>
						<button title="Target angle" className="outline ml-8 h8 icon-node_link_direction" style={{height: '22px'}} onClick={()=>onClick(targetPose[i])} tabIndex="-1"/>
					</div>
				</div>
			</div>
		</div>
	)

	function onClick(pose){
		setResetActive(false)
		onChange({target:{valueAsNumber:pose}})
	}

	function onChange({target:{valueAsNumber}}){
		dispatch(['SET_SIM_ANGLES',{...state.simPose,[i]: valueAsNumber}])
	}
}

function SpeedControls(){
	const [state,setState]=useState({
		velocity: Sixi2Speeds.velocity.default,
		acceleration: Sixi2Speeds.acceleration.default,
	})
	return (
		<Fragment>
			<div className="p-8">
				<div className="h7 text-muted">Angular Velocity</div>
				<div className="row mt-0">
					<div className="col" style={{height: '30px'}}>
						<input
							type="range"
							min={Sixi2Speeds.velocity.min}
							max={Sixi2Speeds.velocity.max}
							value={+state.velocity.toFixed(1)}
							{...{onChange}}
							step=".1"/>
					</div>
				</div>
				<div className="row">
					<div className="col row f-justify-space-between" style={{height: '22px'}}>
						<div className="col text-muted h7">{+Sixi2Speeds.velocity.min.toFixed(1)}</div>
						{/*<div className="col text-center b-500">{state.simPose[i]}</div>*/}
						{/*<input className="text-center py-2" type="number" pattern="-?[0-9]+(\.[0-9]{0,3})?" value={state.simPose[i]} {...{onChange}}/>*/}
						<div className="button-icon f-self-center" style={{height: '20px'}}>
							<div className="icon h7 text-muted" style={{fontFamily: 'inherit'}}>{Sixi2Speeds.velocity.default}</div>
							<div className="p-0">
								<input className="text-center py-2" type="number" value={+state.velocity.toFixed(1)} {...{onChange}}/>
							</div>
						</div>
						<div className="col text-muted h7 text-right">{+Sixi2Speeds.velocity.max.toFixed(1)}</div>
					</div>
				</div>
			</div>
			<div className="p-8">
				<div className="h7 text-muted">Angular Acceleration</div>
				<div className="row mt-0">
					<div className="col" style={{height: '30px'}}>
						<input
							type="range"
							min={Sixi2Speeds.acceleration.min}
							max={Sixi2Speeds.acceleration.max}
							value={+state.acceleration.toFixed(1)}
							{...{onChange}}
							step=".1"/>
					</div>
				</div>
				<div className="row">
					<div className="col row f-justify-space-between" style={{height: '22px'}}>
						<div className="col text-muted h7">{+Sixi2Speeds.acceleration.min.toFixed(1)}</div>
						{/*<div className="col text-center b-500">{state.simPose[i]}</div>*/}
						{/*<input className="text-center py-2" type="number" pattern="-?[0-9]+(\.[0-9]{0,3})?" value={state.simPose[i]} {...{onChange}}/>*/}
						<div className="button-icon f-self-center" style={{height: '20px'}}>
							<div className="icon h7 text-muted" style={{fontFamily: 'inherit'}}>{Sixi2Speeds.acceleration.default}</div>
							<div className="p-0">
								<input className="text-center py-2" type="number" value={+state.acceleration.toFixed(1)} {...{onChange}}/>
							</div>
						</div>
						<div className="col text-muted h7 text-right">{+Sixi2Speeds.acceleration.max.toFixed(1)}</div>
					</div>
				</div>
			</div>
		</Fragment>
	)

	function onChange({target:{valueAsNumber}}){

	}
}
